$main-color: #36d7b7;
$second-color: #ff5656;
$color-1: #1eacc7;

@mixin btn_hover {
	&:hover {
		background-image: none;
		background-color: $main-color; } }



* {
	font-family: 'Roboto',sans-serif; }

header {
	position: relative; }

nav {
	position: fixed; //фиксируем меню сверху.
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 5; } //выводим меню поверх всего сайта.

.menu {
	display: flex;
	justify-content: space-between;


	padding: 13px 70px;

	margin: 0;
	list-style-type: none; }

.menu_link {
	font-size: 15px;
	font-weight: 300;
	color: #ffffff;

	.menu_item {}
	border-right: 2px dotted black;
	padding-right: 28px; }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6); }

.subheader_logo {
	display: block;
	max-width: 100%;
	margin-top: 16px; }

.subheadr_official {
	margin-top: 6px;
	opacity: 0.5;
	font-size: 13px;
	font-weight: 100;
	color: #ffffff; }

.subheader_caal {
	font-size: 12px;
	font-weight: 300;
	color: #ffffff;
	margin-top: 19px;
	text-align: right; }

.subheader_phone {
	display: block;
	margin-top: 8px;
	font-size: 22px;
	font-weight: 400;
	text-transform: uppercase;
	color: #ffffff;
	text-align: right;
	line-height: 12px; }

.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
	border-radius: 5px;
	box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.11);
	box-shadow: 1px 1px 0px 0px rgba(0, 0, 0, 0.2);
	font-size: 14px;
	font-weight: 400;
	text-transform: uppercase;
	color: #ffffff;
	@include btn_hover; }


.promo {
	min-height: 800px;
	background: url('../img/bg/bg_header1.png') center (center / cover) no-repeat;
	padding: 150px 0 135px 0; }

.promo_header {
	font-size: 60px;
	font-weight: 900;
	text-transform: uppercase;
	line-height: 60px;
	color: #ffffff;
	text-align: center;
	margin: 0; }

.promo_subheader {
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	text-transform: uppercase;
	line-height: 36px;
	color: #ffffff;
	margin: 0;
	margin-top: 22px; }

.promo_descr {
	font-size: 18px;
	font-weight: 300;
	line-height: 25px;
	color: #ffffff;
	text-align: center;
	margin-top: 16px; }

.promo_btn {
	display: block;
	margin: 115px auto 0 auto;
	width: 259px;
	height: 63px;
	background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
	border: 3px solid rgba(0, 0, 0, 0.06);
	box-shadow: inset 0px -4px 0px 0px rgba(0, 0, 0, 0.11);
	border-radius: 5px;
	@include btn_hover;



	box-shadow: 1px 1px 0px 0px rgba(0, 0, 0, 0.2);
	font-size: 18px;
	font-weight: 400;
	text-transform: uppercase;
	color: #ffffff; }

.advan {
	padding: 71px 0 65px 0; }

.adv {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	z-index: 2;
	&_1 {
		background-color: #fff;
		color: #000; } }


.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 32px;
	font-size: 38px;
	line-height: 48px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	color: #222222;
	z-index: 2;
	&_1 {
		color: #fff; } }


.subtitle {
	position: relative;
	margin-top: 19px;
	font-size: 17px;
	line-height: 21px;
	font-weight: 400;
	text-align: center;
	color: #222222;
	z-index: 2;
	&_1 {
		color: #fff; } }

.advan .advan_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px; }

.advan_round {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 113px;
	height: 113px;
	margin-right: 26px;
	background-color: #1eacc7;
	border-radius: 100%; }

.advan_descr {
	width: 343px; }

.advan_subtitle {
		font-size: 17px;
		line-height: 21px;
		font-weight: 400;
		color: #1eacc7; }

.advan_text {
		font-size: 15px;
		font-weight: 300;
		line-height: 21px;
		color: #202020; }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/bg/bg_mobile.png') center (center / cover ) no-repeat;
	.row {
		margin-top: 30px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 88px;
		&_1 {
			 background: url('../img/images/mobile_1.png') center (center / cover ) no-repeat; }
		&_2 {
			 background: url('../img/images/mobile_2.png') center (center / cover ) no-repeat; }
		&_3 {
			 background: url('../img/images/mobile_3.png') center (center / cover ) no-repeat; }
		&_4 {
			 background: url('../img/images/mobile_4.png') center (center / cover ) no-repeat; }
		&_5 {
			 background: url('../img/images/mobile_5.png') center (center / cover ) no-repeat; }
		&_6 {
			 background: url('../img/images/mobile_6.png') center (center / cover ) no-repeat; }
		&_text {
			font-size: 24px;
			line-height: 31px;
			font-weight: 400;
			text-transform: uppercase;
			color: #ffffff;
			text-align: center; }
		&-icon {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&::before {
				content: '';
				display: {}
				width: 14px;
				height: 2px;
				background-color: $color-1;
				transition: 0.3s all; }
			&::after {
				content: '';
				display: block;
				left: 50%;
				position: absolute;
				transform: translateX(-50%);
				height: 14px;
				width: 2px;
				background-color: $color-1;
				transition: 0.3s all; }
			&:hover {
				&::before, &::after {
					background-color: $second-color; } } } } }

.choice {
	position: relative;
	min-height: 896px;
	padding-top: 59px;
	&_bg1 {
		position: absolute;
		top: 0;
		z-index: -1; }

	&_bg2 {
		position: absolute;
		bottom: 0;
		z-index: -1; }



	&_car {
		display: flex;
		justify-content: center;
		margin: 0 auto;
		margin-top: 54px; }

	&_carday {
		margin: 0 auto;
		height: 327px;
		font-size: 17px;
		line-height: 24px;
		font-weight: 300;
		color: #222222; }

	h4 {
		font-size: 17px;
		line-height: 24px;
		font-weight: 300;
		color: #222222;
		text-align: center; } }

require {
	padding: 66px 0 87px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0; } }
	&_title {
		font-size: 22px;
		line-height: 24px;
		font-weight: 400;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		font-size: 17px;
		font-weight: 300;
		color: #222222; }
	&_descr {
		font-size: 17px;
		font-weight: 300;
		color: #1ca8c3; } }



